<template>
  <div class="relative">
    <div
      class="w-screen fixed flex justify-between p-4 z-40
    bg-black bg-opacity-30 text-white  
    "
    >
      <button class="p-2  rounded-xl" @click="data.isOpen = !data.isOpen">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-6 "
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            v-if="!data.isOpen"
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M4 6h16M4 12h16M4 18h16"
          />
          <path
            v-else
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M6 18L18 6M6 6l12 12"
          />
        </svg>
      </button>
      <img src="./img/logo-white.png" style="height:36px;" alt="" />
    </div>
    <div
      v-show="data.isOpen"
      class="w-screen h-screen fixed flex bg-black opacity-70 z-30"
    >
      <ul
        class="flex flex-col text-white justify-center items-center w-full"
      >
        <li @click="openLink('/')">Beranda</li>
        <li @click="openLink('/about')">Profil Laboratorium</li>
        <li @click="openLink('/organization')">Struktur Organisasi</li>
        <li @click="openLink('/team')">Asisten Laboratorium</li>
        <li @click="openLink('/alumnus')">Alumni Laboratorium</li>
        <li @click="openLink('/prestasi')">Prestasi</li>
        <li @click="openLink('/research')">Research Topic</li>
        <li @click="openLink('/rg')">Research Group</li>
        <li @click="openLink('/activityint')">Kegiatan Internal</li>
        <li @click="openLink('/activityext')">Kegiatan Eksternal</li>
        <li @click="openLink('/praktikum')">Praktikum</li>
        <li @click="openLink('/verification')">Sertifikat</li>
        <li @click="openLink('/contact')">Kontak</li>
        <li @click="openLink('/blog')">Blog</li>
        <li @click="openLink('/elearning')">E-Learning</li>
        <li @click="openLink('/login')">Login</li>
        <li
          @click="toggleDarkMode"
          class="mt-6 flex gap-4 border rounded-full px-4 py-2"
        >
          <svg
            v-if="darkMode"
            xmlns="http://www.w3.org/2000/svg"
            class="h-8 w-8 inline-block hover:text-pink-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z"
            />
          </svg>
          <svg
            v-else
            xmlns="http://www.w3.org/2000/svg"
            class="h-8 w-8 inline-block fill-current hover:text-pink-600"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z"
            />
          </svg>
          Toggle Dark Mode
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { reactive, computed } from "vue";
import { useRouter, useRoute } from "vue-router";
import { useStore } from "vuex";

export default {
  setup() {
    const store = useStore();

    let data = reactive({
      isOpen: false,
    });

    const router = useRouter();
    let openLink = (url) => {
      router.push(url);
      data.isOpen = false;
    };
    let darkMode = computed(() => store.state.darkMode);
    let toggleDarkMode = () => store.commit("toggleDarkMode");
    return { data, openLink, darkMode, toggleDarkMode };
  },
};
</script>
